import axios from 'axios'

import router from '@/router'

export default {
  state () {
    const userDefault = {
      // persistent to database
      name: '',
      image: '',
      coins: -1,
      score: -1,
      title: '',
      stats: [0, 0, 0],
      achievements: [],
      last_login: '',
      created_on: ''
    }
    return {
      userDefault: userDefault,
      user: JSON.parse(JSON.stringify(userDefault)),
    }
  },
  getters: {
    // eslint-disable-next-line
    user: (state, getters) => {
      return state.user
    }
  },
  mutations: {
    setUser: (state, payload) => {
      state.user = payload
    },
    setUserAttr: (state, payload) => {
      for (let [k, v] of Object.entries(payload)) {
        state.user[k] = v
      }
    }
  },
  actions: {
    setUser: (context, payload) => {
      context.commit('setUser', payload)
    },
    setUserAttr: (context, payload) => {
      context.commit('setUserAttr', payload)
    },
    // eslint-disable-next-line
    async signOut (context, payload) {
      // default user
      context.commit(
        'setUser', JSON.parse(JSON.stringify(context.state.userDefault))
      )
      // set to home page
      router.push({
        name: 'home' 
      })
    },
    // eslint-disable-next-line
    async authenticate (context, payload) {
      return new Promise(
        (resolve, reject) => {
          axios.post(
            '/api/v1/users/authenticate', {
              name: payload.name, password: payload.password
            } 
          ).then(
            response => {
              resolve(response.data)
            }
          ).catch(
            error => {
              reject(error.response.data)
            }
          )
        }
      )
    },
    // eslint-disable-next-line
    async registration (context, payload) {
      return new Promise(
        (resolve, reject) => {
          if (payload.password.length < 8) {
            reject('password must have at least 8 characters!')
          } else if (payload.password !== payload.passwordConfirmed) {
            reject('password must match!')
          } else {
            axios.post(
              '/api/v1/users/registration', {
                name: payload.name, password: payload.password
              } 
            ).then(
              response => {
                resolve(response.data)
              }
            ).catch(
              error => {
                reject(error.response.data)
              }
            )
          }
        }
      )
    },
    // eslint-disable-next-line
    async updatePassword (context, payload) {
      return new Promise(
        (resolve, reject) => {
          if (payload.password.length < 8) {
            reject('password must have at least 8 characters!')
          } else if (payload.password !== payload.passwordConfirmed) {
            reject('password must match!')
          } else {
            axios.post(
              '/api/v1/users/updatePassword', {
                name: payload.name, password: payload.password
              } 
            ).then(
              response => {
                resolve(response.data)
              }
            ).catch(
              error => {
                reject(error.response.data)
              }
            )
          }
        }
      )
    },
    // eslint-disable-next-line
    async updateProperty (context, payload) {
      return new Promise(
        (resolve, reject) => {
          axios.post(
            '/api/v1/users/updateProperty', payload
          ).then(
            response => {
              resolve(response.data)
            }
          ).catch(
            error => {
              reject(error.response.data)
            }
          )
        }
      )
    },
  }
}
